.abc > .kigui-o-f:not(.show) > h3 {
    margin-bottom:17px;
}

.abc > .kigui-o-f > .img_food {
    height:110px;
    width:110px;

    display:block;
    margin: 0 auto;

    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;

    -webkit-transition-property: height;
    -moz-transition-property: height;
    -ms-transition-property: height;
    -o-transition-property: height;
    transition-property: height;
    -webkit-transition-duration: 0.3s;
    -moz-transition-duration: 0.3s;
    -ms-transition-duration: 0.3s;
    -o-transition-duration: 0.3s;
    transition-duration: 0.3s;
}

.abc > .kigui-o-f.show > .img_food{
    height: 0;
}

.abc > .kigui-o-f .img_material {
    height: 22px;
    width: 22px;
}

.abc.open_mode > .kigui-o-f {
    height:270px;
}

:not(.o-abc) .abc{
    display:none;
}
.o-abc > .abc {
    display:block;
}

:not(.o-info) .info {
    display:none;
}
.o-info > .info {
    display:block;
}

.f01, .f02, .f03, .f04, .f05,
.f06, .f07, .f08, .f09, .f10,
