@media all and (max-width:320px) and (orientation: portrait) {
    .abc > .kigui-o-f {
        width:120px;
        height:140px;

        font-size:14px;
    }

    .abc > .kigui-o-f > h3 {
        font-size: 17px;
    }

    .abc.openMode > .kigui-o-f {
        height:260px;
    }
}